	"immigration-laicite": {
		"title": "Immigration, Laïcité",
		"main": true,
		"image": "immigration-laicite",
		"intro": "Pour le FN, depuis plusieurs décennies déjà, le clivage essentiel est celui qui oppose <a href=\"http://www.monde-diplomatique.fr/2017/05/LAMBERT/57493\">« le parti des étrangers contre le parti de la France »</a>, et l&#39;immigration est le problème central et le cœur de toute son argumentation. Marine Le Pen, dont le programme de 2012 prévoyait d&#39; <a href=\"http://www.frontnational.com/pdf/projet_mlp2012.pdf\">« interdire les manifestations de soutien aux clandestins »</a>, voit l&#39;immigration comme un véritable « raz-de-marée », une <a href=\"http://www.lepoint.fr/politique/marine-le-pen-compare-l-afflux-de-migrants-aux-invasions-barbares-14-09-2015-1964773_20.php\">« invasion » comparable à celles des barbares du IVème siècle</a>.</p>\n<p><a href=\"http://www.lefigaro.fr/flash-actu/2017/03/20/97001-20170320FILWWW00355-le-pen-je-veux-arreter-l-immigration.php\">« Arrêter l&#39;immigration légale et illégale »</a> est donc la grande priorité du FN. Avec la fermeture des frontières et le durcissement des conditions d&#39;accès à la nationalité, le parti veut instaurer la <a href=\"http://www.humanite.fr/la-verite-sur-le-programme-du-front-national-631681\">« priorité nationale »</a>. Pour « ériger la citoyenneté française en privilège » et empêcher les immigrés d&#39;accéder aux droits fondamentaux, au profit des français « de souche », il veut d&#39;abord <a href=\"https://www.mediapart.fr/journal/france/200317/fn-la-priorite-nationale-une-doctrine-contraire-aux-fondements-republicains-0\">modifier la constitution</a>. Pour les quelques étrangers qui parviendraient à rester sur le territoire, le FN parle d&#39;« assimilation», un concept « développé dans le contexte des guerres coloniales françaises au XIXe siècle », qui s&#39;appliquait donc aux « indigènes » des colonies françaises, <a href=\"https://www.mediapart.fr/journal/france/230317/programme-du-fn-4-l-identite-nationale-machine-de-guerre-contre-l-islam\">d&#39;après l’historien Yvan Gastaud</a>. Quant aux musulmans, véritables « ennemis intérieurs » pour ce parti, Marion Maréchal-Le Pen expliquait fin 2015 qu&#39;ils devaient <a href=\"http://www.europe1.fr/politique/pour-marion-marechal-le-pen-les-musulmans-ne-peuvent-etre-francais-que-sous-condition-2629283\">« se plier aux mœurs et au mode de vie que l’influence grecque, romaine, et 16 siècles de chrétienté ont façonnés ».</a>",
		"subs": [
			{
				"title": "Immigration",
				"link": "immigration-laicite-immigration"
			},
			{
				"title": "« Priorité Nationale »",
				"link": "immigration-laicite-priorite-nationale"
			},
			{
				"title": "Laïcité",
				"link": "immigration-laicite-laicite"
			}
		],
		"slug": "immigration-laicite"
	},
